import React from "react"
import Navbar from "../components/navbar"
import Blog from "../components/blog"
import BlogVideo from "../components/blog_video"
import BlogLink from "../components/blog_link"
import Graph from "../components/graph"

import Container from 'react-bootstrap/Container'
import {Jumbotron, Row, Col} from "react-bootstrap"

export default function Home() {
  return (
    <div>
      <link
        rel="stylesheet"
        href="https://maxcdn.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css"
        integrity="sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk"
        crossorigin="anonymous"
      />

      <Navbar items={['Blog', 'Resume', 'Projects']} links={['', 'resume', 'projects']} projects={['Ultra Stack', 'Shaders', 'Puzzle Game', 'IK Demos', 'Spline Editor']}/>    

      <Container fluid>
        <br/>
        <Jumbotron>
          <Row>

            <Col>
            <h1>Greetings!</h1>
              <p>
                I'm Gabe - a software engineer and game designer. <br/><br/>
           	
				In 2020 I graduated from Swarthmore College with a B.A. in Computer Science. After graudation
				I worked as a Software Engineer at Northrop Grumman where I developed aeronautics software
				using C++.<br/><br/>

				Now, I'm working toward my M.S. in Interactive Entertainment (Programming Track) at the Florida Interactive Entertainment Academy (UCF FIEA).
              </p>
            </Col>
          </Row>

        </Jumbotron>

     

      </Container>

      <Container fluid>
		<BlogLink date="8/16/2021" title="Fractal Tree Generator Source Code" body="I just uploaded the source code of my fractal tree generator to GitHub! Check it out on my Projects page." 
																				linktext="Projects"
																				link="projects"/>
        <br/>

        <BlogLink date="5/22/2021" title="Creating a Snow Post Process Effect" body="This blog will be dedicated to breaking down the technical details of the snowy weather post-process effect in my upcoming puzzle game. 
                                                                            I implemented this within 
                                                                            my custom engine, but it could be done in any engine that supports custom shaders and post processing..." 
                                                                            linktext="Read More"
                                                                            link="snow_effect"/>
        <br/>

        <BlogVideo date="4/27/2021" title="'Portal' System Demo" body="I've had my nose to the grindstone and am making fast progress on the engine. I recently added a system that will load new maps when the player enters a specific tile.
                                                                        One application of this is for entering/exiting buildings, where the interior and exterior are separate map files. I put together a short video to demonstrate this. Enjoy!" video="https://www.youtube.com/embed/aWF_azFEgNI" />
        <br/>

        <BlogVideo date="4/24/2021" title="NATURAL Engine Map Editor" body="I've been working on a tile-based 2D engine using C++, OpenGL, and SDL2 (for input/audio). For now, I'm calling it NATURAL Engine. This is a short demo
                                                                          of the engine's map editor. Not shown in this video is its ability to load and save maps in a binary format." video="https://www.youtube.com/embed/rV9T1dKSBs0" />
        <br/>

        <Blog date="2/09/2021" title="First post." body="Welcome! I guess this is my first post." />
        <br/>

      </Container>

      <br/>
    </div>
  )
}
