import React, { Component } from 'react';
import '../../node_modules/react-vis/dist/style.css';
import {XYPlot, XAxis, YAxis, LineSeries, Line, VerticalBarSeries, VerticalGridLines, HorizontalGridLines} from 'react-vis';

class Graph extends Component {

    constructor(props) {
        super(props);
        this.state = {
            scores: ''
        };
      }

    componentDidMount() {
        fetch("http://127.0.0.1:5000/api/scores/all")
            .then(res => res.json())
            .then(result => {
                this.setState({
                    scores: result
                })
            })
            .catch(err => { console.log(err.message)});
    }

    render() {
        const { raw, scores } = this.state;
        console.log("hi 1 ", scores);

        var data = [];
        var yMax = 0;

        if(scores){
            var vScores = scores;
            vScores = vScores.replace(/'/g, "\"");
            const jsonData = JSON.parse(vScores);

            for(var i=0; i < jsonData.length; i++) {
                var element = jsonData[i];
                var freq = element['Freq'];

                if(freq > yMax){
                    yMax = freq;
                }

                data.push({x: element['Score'], y: freq});
            }
        }

        return (
        <div>
            <XYPlot height={300} width={300} yDomain={[0, yMax + 1]}>

            <VerticalGridLines />
            <HorizontalGridLines />
            <YAxis title = "Frequency" />
            <XAxis title = "Score" />
            <LineSeries className="third-series" curve={'curveMonotoneX'} data={data} />
            </XYPlot>
        </div>
        );
    }
}

export default Graph;
